@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --clr-white: #fff;
  --clr-lightgrey: #e6e6e6;

  --clr-dark-blue: #002561;
  --clr-medium-blue: #457fca;

  --clr-orange: #ff6347;

  --clr-medium-black: #616161;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #fff;
  background: var(--clr-white);
}

::-webkit-scrollbar-thumb {
  background: #ff6347;
  background: var(--clr-orange);
  border-radius: 8px;
}

.search-form {
  min-height: 150px;

  padding: 20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 16px;
  gap: 16px;

  color: #fff;

  color: var(--clr-white);

  background: #457fca;

  background: var(--clr-medium-blue);
}

.search-form h2 {
  font-weight: 300;
}

.search-bar,
.search-btn {
  padding: 10px;
  border: none;

  font-size: 16px;

  transition: background 0.2s ease;
}

.search-bar {
  width: 300px;

  border-radius: 4px 0 0 4px;
}

.search-bar:hover,
.search-bar:focus {
  background: #e6e6e6;
  background: var(--clr-lightgrey);
}

.search-btn {
  padding: 10px 12px;

  border: none;
  border-radius: 0 4px 4px 0;

  color: #fff;

  color: var(--clr-white);
  font-weight: 700;

  background: #ff6347;

  background: var(--clr-orange);
}

.search-btn:hover,
.search-btn:focus {
  background: rgb(255, 73, 41);
}

.recipe-container {
  min-height: 100vh;
  padding: 4rem;

  display: grid;
  justify-content: center;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 4rem;
  gap: 4rem;

  background: #e6e6e6;

  background: var(--clr-lightgrey);
}

@media only screen and (max-width: 1024px) {
  .recipe-container {
    padding: 3rem;

    grid-gap: 3rem;

    gap: 3rem;
  }
}

@media only screen and (max-width: 824px) {
  .recipe-container {
    padding: 4rem;

    grid-template-columns: auto;
  }
}

@media only screen and (max-width: 480px) {
  .search-bar {
    width: 250px;
  }

  .recipe-container {
    padding: 2rem;

    grid-gap: 2rem;

    gap: 2rem;
  }

  .recipe-box h1 {
    font-size: 28px;
  }
}

@media only screen and (max-width: 378px) {
  .search-form h2 {
    font-size: 20px;
  }

  .search-bar {
    width: 200px;
  }

  .recipe-container {
    padding: 2rem 1rem;
  }

  .recipe-box h1 {
    font-size: 28px;
  }
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}

.recipe-box {
  padding: 3rem 2rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  grid-gap: 2rem;
  gap: 2rem;

  color: var(--clr-medium-black);

  background: var(--clr-lightgrey);
  border-radius: 12px;

  box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.3),
    inset -3px -3px 8px rgb(255, 255, 255);
}

.recipe-box h1 {
  text-align: center;

  color: #000;
}

.recipe-box ol {
  list-style-position: inside;

  display: flex;
  flex-direction: column;
  grid-gap: 6px;
  gap: 6px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.content p span {
  font-weight: bold;
  color: #000;
}

.content img {
  height: 200px;
  width: 200px;

  border-radius: 50%;
}

@media only screen and (max-width: 378px) {
  .recipe-box {
    padding: 2rem 1rem;
  }
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}

.header {
  padding: 12px 20px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: var(--clr-white);
  font-family: "Berkshire Swash", cursive;
  font-size: 48px;

  border-bottom: 1px solid rgba(255, 255, 255, 0.55);

  background: linear-gradient(var(--clr-dark-blue), var(--clr-medium-blue));
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);
            transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);
            transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

@keyframes spinner {
  0% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);
            transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);
            transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

.spin {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.spin::before {
  -webkit-animation: 1.5s linear infinite spinner;
          animation: 1.5s linear infinite spinner;
  -webkit-animation-play-state: inherit;
          animation-play-state: inherit;
  border: solid 5px #cfd0d1;
  border-bottom-color: #ff6347;
  border-radius: 50%;
  content: " ";
  height: 40px;
  width: 40px;
  position: absolute;
  top: 10%;
  left: 10%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  will-change: transform;
}

